import { useState } from "react"
import "./index.scss"
import axios from "axios"
import logo from "../../static/img/logo.png"
import { Link, NavLink } from "react-router-dom"
import { useForm } from "react-hook-form"
function SignUp() {
  const [signUpError, setSignUpError] = useState("")
  const [signUpSuccess, setSignUpSuccess] = useState(false)

  const {
    register,
    formState: { errors },
    handleSubmit,
    setError,
    watch,
  } = useForm()
  const config = {
    headers: {
      "Content-Type": "application/json",
    },
    withCredentials: true,
  }

  const onSubmit = ({ email, password, nickname }) => {
    console.log(email, nickname, password)
    console.log(" ")
    setSignUpError("")
    setSignUpSuccess(false)
    //      (              )
    getSignUp(email, password, nickname)
  }
  const getSignUp = (email, password, nickname) => {
    axios
      .post(
        "/login",
        JSON.stringify({
          username: email,
          // nickName: nickname,
          password: password,
        }),
        config
      )
      .then((response) => {
        // 
        console.log(response)
        setSignUpSuccess(true)
        alert(" !")
      })
      .catch((error) => {
        // 
        console.log(error.response)
        alert(" !")
        console.log(" api  ")
        console.log(email, password, nickname)
        setSignUpError(error.response.data)
      })
  }
  return (
    <div className="login">
      <Link to="/">
        <img
          className="login-modal__logo"
          src={logo}
          alt="daily-mission-logo"
        ></img>
      </Link>
      <form class="login_box" onSubmit={handleSubmit(onSubmit)}>
        <input
          type="text"
          {...register("email")}
          placeholder=" ."
          name="email"
          // onChange={this.handleInput}
        />
        {/* <input
          type="text"
          placeholder=" ."
          name="name"
          // onChange={this.handleInput}
        /> */}
        <input
          type="text"
          {...register("nickname")}
          placeholder=" ."
          name="nickname"
          // onChange={this.handleInput}
        />
        <input
          type="password"
          {...register("password")}
          placeholder=" ."
          name="password"
          // onChange={this.handleInput}
        />
        <button type="submit"></button>
        <br></br>
        <a>? ?</a>
        <Link to="/login"> </Link>
      </form>
    </div>
  )
}

export default SignUp
