import { useState } from "react";
import { useForm } from "react-hook-form";
import "./index.scss";
import axios from "axios";
import logo from "../../static/img/logo.png";
import { Link, NavLink } from "react-router-dom";
import jwt_decode from "jwt-decode";
const Login = () => {
  const config = {
    headers: {
      "Content-Type": "application/json",
    },
    withCredentials: true,
  };
  const {
    register,
    formState: { errors },
    handleSubmit,
    setError,
    watch,
  } = useForm();

  const onSubmit = ({ email, password }) => {
    getLogin(email, password);
  };
  // API 
  const getLogin = (email, password) => {
    console.log(email, password);

    axios
      .post(
        `/api/v1/login`,
        JSON.stringify({
          username: email,
          password: password,
        }),
        config
      )
      .then(loginSuccess)
      .catch((error) => {
        console.log(error);
      });
  };
  //  
  async function loginSuccess(response) {
    console.log(" ");
    alert(" !");
    //     
    localStorage.removeItem("accessToken");
    localStorage.removeItem("refreshToken");
    const { authorization, authorization_refresh } = response.headers;
    //localStorage  
    localStorage.setItem("accessToken", authorization);
    localStorage.setItem("refreshToken", authorization_refresh);
    console.log(response);
    // jwtDecode(authorization);
  }
  return (
    <div className="login">
      <Link to="/">
        <img
          className="login-modal__logo"
          src={logo}
          alt="daily-mission-logo"
        ></img>
      </Link>
      <form class="login_box" onSubmit={handleSubmit(onSubmit)}>
        <input
          type="text"
          {...register("email")}
          placeholder=" ."
          name="email"
        />
        <input
          type="password"
          {...register("password")}
          placeholder=" ."
          name="password"
        />
        <button type="submit"></button>
        <br></br>
        <Link to="/signUp"> ?</Link>
        <a href="#"> ?</a>
      </form>
    </div>
  );
};

export default Login;
